.product-card {
    width: 220px;
    height: 370px;
    padding: 23px;
    background-color: white;
    flex: 1 1 auto;
    border-radius: 5px;
    overflow: hidden;
    margin: 0.5%;
    border: 1px solid lightgrey;
}

.product-card img {
    width: 100%;
    height: 70%;
}

.product-card h3 {
    font-size: 22px;
    font-weight: bold;
    margin: 10px;
}

.product-card p {
    font-size: 16px;
    margin: 10px;
}

h2 {
    padding-top: 20px;
    display: flex;
    justify-content: center;
}

.main_page {
    padding-top: 50px;
    display: flex;
    justify-content: center;
}

.container {
    width: 90%;
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
}

.error-text {
    color: red;
}

.login-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
}

.page-title {
    margin-bottom: 2rem;
}

.error-text {
    color: red;
    margin-bottom: 1rem;
}

.form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 50%;
    padding: 2rem;
    border: 1px solid lightgray;
    border-radius: 5px;
}

.form-label {
    margin-bottom: 0.5rem;
}

.form-input {
    width: 100%;
    padding: 0.5rem;
    margin-bottom: 1rem;
    border: 1px solid lightgray;
    border-radius: 5px;
}

.form-button {
    padding: 0.5rem 1rem;
    background-color: lightblue;
    border: none;
    border-radius: 5px;
    color: white;
    cursor: pointer;
}

.form-text {
    margin-top: 1rem;
    text-align: center;
}

.detail-page {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;

}

.image-container {
    width: 40%;
    text-align: center;
}

.image-container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.info-container {
    width: 60%;
    padding: 30px;
    background-color: white;
    box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    text-align: left;
}

.product-name {
    font-size: 24px;
    margin-bottom: 10px;
}

.product-price {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 20px;
}

.product-description {
    font-size: 16px;
    margin-bottom: 20px;
}

.product-details {
    margin-bottom: 20px;
}

.product-details h3 {
    font-size: 18px;
    margin-bottom: 10px;
}

.add-to-cart-button {
    width: 100%;
    padding: 10px;
    background-color: blue;
    color: white;
    border: none;
    border-radius: 10px;
    font-size: 18px;
    cursor: pointer;
}

.cart-button {
    display: inline-block;
    background-color: #4CAF50;
    color: white;
    padding: 10px 10px;
    margin-bottom: 5px;
    text-align: center;
    text-decoration: none;
    border-radius: 4px;
    border: none;
    font-size: 8px;
    cursor: pointer;
}